.product-card {
  width: 250px; /* Ancho fijo */
  background-color: #fff;
  padding: 0.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  font-family: sans-serif;
}

.product-card img {
  width: 100%;
  height: auto;
  aspect-ratio: 5 / 6;
  object-fit: cover;
  border-radius: 4px;
  display: block;
  margin: 0 auto;
}

.card-info {
  padding: 0.5rem 0;
}

.category-label {
  font-size: 0.7rem;
  color: #999;
}

h3 {
  font-size: 1rem;
  margin: 0.2rem 0;
}

.color-dots {
  display: flex;
  gap: 5px;
  margin: 0.5rem 0 0.3rem 0;
}

.color-dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid #aaa;
  cursor: pointer;
  transition: all 0.2s ease;
}

.color-dot:hover {
  border: 1px solid #333;
  transform: scale(1.05);
}

.color-dot.selected {
  border: 2px solid #000;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
}

.size-price-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.5rem;
}

.size-tag {
  font-size: 0.72rem;
  margin-right: 4px;
  background-color: #f8f8f8;
  padding: 3px 7px;
  border-radius: 4px;
  cursor: pointer;
  border: 1px solid #ccc;
  transition: all 0.2s ease;
}

.size-tag:hover {
  background-color: #e0e0e0;
}

.size-tag.selected {
  background-color: #111;
  color: #fff;
  border-color: #111;
}

.price {
  font-weight: bold;
  font-size: 0.9rem;
  text-align: right;
  flex-shrink: 0;
}

.add-to-cart-btn {
  width: 100%; /* Botón ocupa todo el ancho */
  margin-top: 0.5rem;
  padding: 0.5rem;
  background-color: #222; /* Negro elegante */
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-to-cart-btn:hover {
  background-color: #444;

}

.add-to-cart-btn:active {
  transform: scale(0.95);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}